import react from 'react'
import axios from 'axios'
import Router from 'next/router'


class Home extends React.Component {
    constructor(props){
        super(props)

    }
    componentDidMount(){
        axios
            .get('http://localhost/TEST/syslogin.php')
            .then((resp) => {
                if(resp.data == "Fail"){
                    Router.push('http://localhost:3000')
                }
                
            })
           
    }

    render(){
        return(
            <div>
                <h1>
                    Login Successfully
                   

                </h1>
            </div>
        )
    }
}

export default Home